import PlanoCard from '../components/planos/CardPlano';
import Sidebar from '../components/SideBar';
import '../styles/planosStyle/Planos.css';
import InfoPlano from '../components/planos/InfoPlano';

export default function Planos() {
  return (
    <div style={{ display: 'flex', background: '#f5f5f5' }}>
      <Sidebar />
      <div className="planos-container">
        <div style={{ padding: '40px 60px' }}>
          <h2>Escolha o Plano Ideal para Voc</h2>
          <p>Compare nossos planos e encontre o que melhor atende suas necessidades.</p>
          
          <InfoPlano />

          <div className="cards-wrapper">
            <PlanoCard
              titulo="Bsico"
              subtitulo="Para iniciantes"
              tipo="bsico"
              beneficios={[
                'Dashboard bsico',
                'At 3 cidades',
                'Relatrios mensais',
                'Suporte por email'
              ]}
              desabilitados={[
                'Mapa interativo',
                'API de integrao'
              ]}
              onClick={() => alert('Contato - Bsico')}
            />

            <PlanoCard
              titulo="Profissional"
              subtitulo="Para corretores"
              destaque
              tipo="profissional"
              beneficios={[
                'Dashboard completo',
                'Cidades ilimitadas',
                'Relatrios semanais',
                'Suporte prioritrio',
                'Mapa interativo'
              ]}
              desabilitados={[
                'API de integrao'
              ]}
              onClick={() => alert('Contato - Profissional')}
            />

            <PlanoCard
              titulo="Empresarial"
              subtitulo="Para imobilirias"
              tipo="empresarial"
              beneficios={[
                'Dashboard premium',
                'Mltiplas cidades',
                'Relatrios dirios',
                'Suporte 24/7',
                'Mapa avanado',
                'API de integrao'
              ]}
              onClick={() => alert('Contato - Empresarial')}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
